import {
  LOCUM_USER_DATA,
  NOTIFICATION_DATA,
  PHARMACY_USER_DATA,
} from "../actions/actionType";

const initialState = {
  notificationData: [],
  locumData: [],
  pharmacyData: [],
};

function persistReducer(state = initialState, action) {
  switch (action.type) {
    case NOTIFICATION_DATA:
      return {
        ...state,
        notificationData: state.notificationData.push(action.payload),
      };

    case PHARMACY_USER_DATA:
      return {
        ...state,
        pharmacyData: action.payload,
      };

    case LOCUM_USER_DATA:
      return {
        ...state,
        locumData: action.payload,
      };
    default:
      return state;
  }
}

export default persistReducer;
