import React, { useCallback, useEffect, useState } from "react";
import { ROUTE_CONSTANTS } from "../../../../shared/routeConstants";
import { useHistory, useLocation } from "react-router-dom";
import {
  BRANCHES_VIEW,
  NA,
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  TAB_BRANCHES_VIEW,
} from "shared/constants";
import NavTab from "components/atom/tabNav";
import BasicView from "./BasicView";
import { useDispatch } from "react-redux";
import useNotification from "components/atom/snackbar";
import { branchViewApi, deleteBranchsApi } from "redux/actions/auth";
import BreadCrumb from "components/atom/breadcrum";

function BranchesView({}) {
  // Importing required hooks and functions from React.
  const history = useHistory();
  const dispatch = useDispatch();

  // Retrieving the id of the branch from the location object passed through the route.
  const idParam = useLocation();

  // State variables for the branch details and notification messages.
  const [activeItem, setActiveItem] = useState(1);
  const id = idParam?.state?.id;
  const [viewData, setViewData] = useState({});
  const [msg, sendNotification] = useNotification();
  const BASIC_CONST = [
    {
      title: "Pharmacy Name",
      value: viewData?.pharmacy_name ? viewData?.pharmacy_name : NA,
      isVerified: parseInt(viewData?.verified_document) > 100 ? true : parseInt(viewData?.verified_document),
    },
    {
      title: "Branch Name",
      value: viewData?.branch_name ? viewData?.branch_name : NA,
    },
    {
      title: "Address",
      value: viewData?.address ? viewData?.address : NA,
    },
    {
      title: "Phone",
      value: viewData?.phone_number ? "+" + viewData?.phone_number : NA,
    },
    {
      title: "Branch Email",
      value: viewData?.branch_email ? viewData?.branch_email : NA,
      isVerified: viewData?.verified,
    },
    {
      title: "Pharmacy Email",
      value: viewData?.pharmacy_email ? viewData?.pharmacy_email : NA,
      isVerified: viewData?.pharmacy_verified,
    },
    {
      title: "Manager Name",
      value: viewData?.manager_info?.manager_name
        ? viewData?.manager_info?.manager_name
        : NA,
    },
    {
      title: "Manager Phone",
      value: viewData?.manager_info?.manager_phone
        ? viewData?.manager_info?.manager_phone
        : NA,
    },
    {
      title: "Manager Email",
      value: viewData?.manager_info?.manager_email
        ? viewData?.manager_info?.manager_email
        : NA,
    },
    {
      title: "Number of Staff",
      value: viewData?.number_of_staff ? viewData?.number_of_staff : NA,
    },
  ];

  const deleteBranch = (id) => {
    // Creating an object with the id of the current branch to be deleted.
    let data = {
      branches: [
        {
          id: id,
        },
      ],
    };
    // Dispatching an API call to delete the branch from the database.
    dispatch(
      deleteBranchsApi(data, (res) => {
        // If the deletion is successful, displaying a success notification message.
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        // If the deletion is unsuccessful, displaying an error notification message.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const DataSwitch = () => {
    switch (activeItem) {
      case BRANCHES_VIEW.BASIC_BRANCH_INFO.ID:
        return <BasicView data={BASIC_CONST} apiData={viewData} />;
      case BRANCHES_VIEW.VACANCIES.ID:
        return <>No Data</>;
      default:
    }
  };

  // Function to retrieve the details of the current branch from the database.
  const viewUser = useCallback(() => {
    // Creating an object with the id of the current branches to be retrieved.
    let data = { id: id };
    // Dispatching an API call to retrieve the branches details from the database.
    dispatch(
      branchViewApi(data, (res) => {
        // If the retrieval is successful, updating the state variable with the branches details.
        setViewData(res.data);
      }),
      (err) => {
        // If the retrieval is unsuccessful, displaying an error notification message and navigating back to the previous page.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  // useEffect hook to retrieve the branches details on page load
  useEffect(() => {
    viewUser();
  }, [viewUser]);

  return (
    <>
      <div className="d-flex flex-wrap align-items-center justify-content-between">
        <BreadCrumb
          onClick={() => history.push(ROUTE_CONSTANTS.USERS)}
          previousLink={PHARMACY_USER_VIEW.PHARMACY_BRANCHES}
          currentLink={PHARMACY_USER_VIEW.DETAILS}
        />
      </div>
      <NavTab
        onClick={(id) => setActiveItem(id)}
        tabsData={TAB_BRANCHES_VIEW}
        activeTab={activeItem}
      />
      <DataSwitch />
    </>
  );
}

export default BranchesView;
